export default {
	data() {
		return {
			bgCoverlist: [],
			bgCovername: '',
			bgCoverIndex: -1
		}
	},
	onLoad() {
		this.getbgCoverList()
		this.getUsebgCover()
	},
	methods: {
		// 获取硬币列表
		getbgCoverList() {
			let list = [{
					name: '烟雨江南',
					src: 'bg_4'
				},
				{
					name: '日系田园',
					src: 'bg_5'
				},
				{
					name: '璀璨星空',
					src: 'bg_12'
				},
				{
					name: '浪漫樱花',
					src: 'bg_14'
				},
				{
					name: '漫步云海',
					src: 'bg_15'
				}
			]
			this.bgCoverlist = []
			for (let item of list) {
				let data = {
					name: item.name,
					id: item.src,
					src: `https://tosscoin-1256354221.file.myqcloud.com/img/${item.src}.jpg`
				}
				this.bgCoverlist.push(data)
			}
		},
		// 获取当前使用背景
		getUsebgCover() {
			this.bgCovername = uni.getStorageSync('bgCoverImg') || 'bg_4'
		},
		// 选中硬币
		selectbgCover(item, index) {
			this.bgCoverIndex = index
			this.bgCoverId = item.id
		},
		// 设置硬币皮肤
		setbgCover() {
			uni.setStorageSync('bgCoverImg', this.bgCoverId);
			uni.showToast({
				title: '设置成功',
				duration: 2000
			});
			this.getUsebgCover()
		}
	}
}
